import CONSTS from './action-names';
const {THEME_CHANGE} = CONSTS;

import defaultState from './default-state';

interface IArticle {
    article: string;
    title: string;
    date: string;
}

interface IState {
    type: string;
    theme: string;
    articles: IArticle[];
}

export default function reducer(state = defaultState, {type, theme}: IState) {
    switch (type) {
        case THEME_CHANGE: {
            return {
                ...state,
                theme: theme || state.theme
            };
        }
        default: {
            return state;
        }
    }
}