import React, {Component} from 'react';
import {cn} from '@bem-react/classname';

import CONSTS from '../../modules/action-names';
const {THEME_CHANGE} = CONSTS;

import './Header.css';

import {connect} from 'react-redux';
import {changeTheme} from '../../modules/actions';

interface ILink {
    title: string;
    href: string;
    icon?: string;
}

interface IHeaderProps {
    logo: string;
    links?: ILink[];
    theme?: string;
    changeTheme?: (obj: any) => any;
}

class Header extends Component<IHeaderProps> {
    private links: JSX.Element[];

    private cnHeader = cn('Header');
    private cnTheme = cn('Theme');
    private cnNav = cn('Nav');

    private logoRef: HTMLImageElement | null | undefined;

    constructor(props: IHeaderProps) {
        super(props);
        this.links = this.createNavItems(props.links || []);
    }

    private createNavItems(links: ILink[]) {
        const {cnNav, cnHeader, Logo} = this;
        const center = Math.floor(links.length / 2);

        const res: JSX.Element[] = links.map(({href, title, icon}, key) => (
            <a target='_blank' href={href} className={cnNav('Item')} key={key}>
                {icon
                    ? <img title={title} alt={title} className={cnHeader('Icon')} src={icon} />
                    : <span className={cnHeader('Span')}>{title}</span>
                }
            </a>
        ));

        const rightPart = res.splice(center);
        res.push(Logo);

        return res.concat(rightPart);
    }

    private setLogoInteractive() {
        const {logoRef} = this;

        if (
            !logoRef ||
            /Android|webOS|iPhone|iPad|iPod|BlackBerry|BB|PlayBook|IEMobile|Windows Phone|Kindle|Silk|Opera Mini/i.test(navigator.userAgent)
        ) return;

        let prevTransform: string = '';

        logoRef.addEventListener('mousemove', ({clientX: x, clientY: y}) => {
            let rX: string, rY: string;
            const {offsetHeight: height, offsetWidth: width, offsetLeft: left, offsetTop: top} = logoRef;

            const leftPart = left + width / 2;
            const topPart = top + height / 2;

            if (x > leftPart) rY = '30deg';
            else rY = '-30deg';

            if (y > topPart) rX = '-30deg';
            else rX = '30deg';

            const res = `rotateX(${rX}) rotateY(${rY})`;
            if (res === prevTransform) return;

            prevTransform = res;
            logoRef.style.transform = res;
        });

        const clearStyles = () => (logoRef.style.transform = '', prevTransform = '');
        logoRef.addEventListener('mouseout', clearStyles);
        logoRef.addEventListener('mouseleave', clearStyles);
    }

    private get Logo(): JSX.Element {
        return <img ref={ref => (this.logoRef = ref, this.setLogoInteractive())} key={-1} src={this.props.logo} className={this.cnHeader('Logo')} />
    }

    private createThemeHandler(theme: string) {
        const {changeTheme} = this.props;

        return function () {
            changeTheme && changeTheme({type: THEME_CHANGE, theme});
        };
    }

    private createThemeTab(themeName: string) {
        const {theme} = this.props;

        return (
            <div className={this.cnTheme({active: theme === themeName})} onClick={this.createThemeHandler(themeName)}>
                {themeName[0].toUpperCase() + themeName.slice(1)}
            </div>
        );
    }

    render() {
        return (
            <header className={this.cnHeader()}>
                <nav className='Themes'>
                    {this.createThemeTab('night')}
                    {this.createThemeTab('day')}
                </nav>
                <nav className={this.cnNav()}>
                    {this.links}
                </nav>
            </header>
        );
    }
}

export default connect((state: any) => ({...state}), {changeTheme})(Header);